import eHeader from '../../components/electrictyPower/electPowerTop'
import eFoot from '../../components/electrictyPower/electPowerFoot'

export default {
  data () {
    return {
      electType: 1
    }
  },
  created () {

  },
  mounted () {
    console.log(this.$echarts, '333')
    console.log(window, '222')
  },
  methods: {
    tableSelt (type) {
      this.electType = type;
      this.$router.push(this.linkName)
    }
  },
  components: {
    eHeader,
    eFoot
  },
  computed: {
    linkName () {
      return this.electType == 1 ? '/electricityPower/mothIndex' : '/electricityPower/dayIndex'
    }
  }
}



